import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import ClickButton from "../components/atoms/ClickButton";
import Header from "../components/layout/Header";
import styled from "styled-components";
import axios from "axios";

const PageContainer = styled.div`
    width: 100%; /*       */
    width: 1920px;
    position: relative; /*     */
    display: flex;
    flex-direction: column;
    align-items: center; /*     */
`;

const WorkerMappingWrapper = styled.div`
    width: 100%;
    min-height: 840px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
`;

const InputWorkerWrapper = styled.div`
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-wrap: wrap;
`;

const ScannerBox = styled.div`
    width: 200px;
    height: 450px;
    text-align: center;
    margin: 20px;
`;

const ScannerImage = styled.img`
    width: 100%;
    margin-bottom: 10px;
`;

const InputField = styled.input`
    width: 90%;
    padding: 10px;
    font-size: 14px;
    text-align: center;
    border: 1px solid #ddd;
    border-radius: 5px;
    outline: none;
    display: ${(props) => (props.editable ? "block" : "none")}; /*    */
`;

const WorkerName = styled.h4`
    margin: 0;
    white-space:nowrap;
    /* font-size:20px; */
    display: ${(props) => (props.editable ? "none" : "block")}; /*    */
`;

const ButtonWrapper = styled.div`
    display: flex;
    justify-content: center;
    gap: 20px;
    margin-top: 30px;
`;

const ActionButton = styled.button`
    width: 160px;
    height: 60px;
    padding: 10px 20px;
    font-size: 20px;
    font-weight: bolder;
    border-radius: 10px;
    cursor: pointer;
    border: none;
    color: #fff;
    background: #1B263B;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 30px;

    &:hover {
        opacity: 0.8;
    }
`;
const XButton = styled.img`
    position:absolute;
    width: 60px;
    height: 60px;
    top: 160px; /*   */
    right: calc((100% - 1920px) / 10 + 60px); /*      30px  */
    z-index: 1;

`;
function WorkerMapping() {
    const [workerData, setWorkerData] = useState([
        { scanner_id: 1, worker: "" },
        { scanner_id: 2, worker: "" },
        { scanner_id: 3, worker: "" },
        { scanner_id: 4, worker: "" },
        { scanner_id: 5, worker: "" },
    ]);
    const [isEditing, setIsEditing] = useState(false);
    const [username, setUsername] = useState("Guest");
    
    useEffect(() => {
        const storedUsername = localStorage.getItem("username"); //  "username"   
        if (storedUsername) {
            setUsername(storedUsername); //    
        }
    }, []);

    //   
    useEffect(() => {
        const fetchWorkerData = async () => {
            try {
                const response = await axios.get("http://localhost:8080/api/worker/setworker");
                setWorkerData(response.data); //      
            } catch (error) {
                console.error("Error fetching worker data:", error);
                alert("     .");
            }
        };

        fetchWorkerData();
    }, []);

    const handleInputChange = (scannerId, value) => {
        setWorkerData((prevData) =>
            prevData.map((item) =>
                item.scanner_id === scannerId ? { ...item, worker: value } : item
            )
        );
    };

    const handleEdit = () => {
        setIsEditing(true);
    };

    const handleSave = async () => {
        try {
            await axios.patch("http://localhost:8080/api/worker/saveworker", workerData); // workerData   
            alert("   .");
            setIsEditing(false);
        } catch (error) {
            console.error("Error saving worker data:", error);
            alert("     .");
        }
    };

    const handleCancel = () => {
        setIsEditing(false);
    };

    return (
        <>
            

            <PageContainer>
                <Header title="   "         
                username={username} //     
                />
                <WorkerMappingWrapper>

                <Link to="/Home"><XButton src="/assets/Icon/Main/Xcircle.svg" /></Link>
                    
                    <InputWorkerWrapper>
                        {workerData.map((item) => (
                            <ScannerBox key={item.scanner_id}>
                                <h2>BLE Scanner {item.scanner_id}</h2>
                                <ScannerImage
                                    src="/assets/Icon/Main/BeaconScanner.svg"
                                    alt={`Scanner ${item.scanner_id}`}
                                />
                                <WorkerName editable={isEditing}>
                                    Worker Name : {item.worker || "N/A"}
                                </WorkerName>
                                <InputField
                                    type="text"
                                    placeholder=" "
                                    value={item.worker || ""}
                                    onChange={(e) => handleInputChange(item.scanner_id, e.target.value)}
                                    editable={isEditing}
                                />
                            </ScannerBox>
                        ))}
                    </InputWorkerWrapper>
                    <ButtonWrapper>
                        {!isEditing ? (
                            <ClickButton onClick={handleEdit} text="" />
                        ) : (
                            <>
                                <ActionButton onClick={handleSave}></ActionButton>
                                <ActionButton onClick={handleCancel}></ActionButton>
                            </>
                        )}
                    </ButtonWrapper>
                </WorkerMappingWrapper>
            </PageContainer>
        </>
    );
}

export default WorkerMapping;
