import React, { useState, useEffect } from "react";
import Header from "../components/layout/Header";
import styled from "styled-components";
import { Link } from "react-router-dom";
import PlaceButton from "../components/atoms/PlaceButton";

const PageContainer = styled.div`
    width: 100%; /*       */
    width: 1920px;
    display: flex;
    flex-direction: column;
    position: relative; /*    */

    align-items: center; /*     */
`;

const SearchPlaceWrapper = styled.div`
    width: 100%;
    min-height: 840px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    position: relative; /*    */
    flex-wrap: wrap; /*   */
`;

const BeaconScannerButton = styled.img`
    z-index: 1;
    position: absolute; /*  (PageContainer)    */
    width: 80px;
    height: 120px;
    /* 1920px       */
    right: max(calc((100% - 1920px) / 2 + 140px), 140px); /* 120px   */
    top: 160px; /*   */
`;

function Home() {
    const [username, setUsername] = useState("Guest");
    
    useEffect(() => {
        const storedUsername = localStorage.getItem("username"); //  "username"   
        if (storedUsername) {
            setUsername(storedUsername); //    
        }
    }, []);


    return (
        <>
            
            <PageContainer>
                <Header
                    title=" "
                    username={username} //     
                />
                <Link to="/WorkerMapping">
                        <BeaconScannerButton src="/assets/Icon/Main/BeaconScannerIcon.svg" />
                    </Link>

                <SearchPlaceWrapper>
                    
                    <PlaceButton placeName="7" placeImg="/assets/Icon/Location/Place_7.svg" />
                    <PlaceButton placeName="" placeImg="/assets/Icon/Location/LibraryLocationPin.svg" />
                </SearchPlaceWrapper>
            </PageContainer></>
    );
}

export default Home;
