import React, { useState, useEffect } from "react";
import Header from '../components/layout/Header';
import Navbar from '../components/molecules/Navbar';
import styled from 'styled-components';


const MainWrapper = styled.div`
    display: flex; /* Flexbox     */
    width: 1920px; /*    */    
    margin-top: 15px;
    margin-bottom:15px;

    height: 870px; /*   */
    margin: 0 auto; /*   */
    align-items: center; /*    */
    position: relative; /* Overlay    relative  */
`;

const BlurredMapImageWrapper = styled.div`
    flex: 1; /*     */
    margin-right:30px;
    display: flex;
    justify-content: center; /*    */
    align-items: center; /*    */
    position: relative; /* Overlay  relative */
`;

const BlurredMapImage = styled.img`
    width: 1083px;
    height: 840px;
    filter: ${(props) => (props.blur ? "blur(30px)" : "none")}; /* blur     */
`;

const SoonOverlay = styled.img`
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 200px; /* Soon   */
    height: auto;
    z-index: 1;
`;

function Preparing() {
    const [username, setUsername] = useState("Guest");
    
    useEffect(() => {
        const storedUsername = localStorage.getItem("username"); //  "username"   
        if (storedUsername) {
            setUsername(storedUsername); //    
        }
    }, []);
    return (
        <>
            <Header 
                title="  " 
                username={username} //     
            />
            <MainWrapper>
                {/*  */}                    
                <Navbar 
                        middleIcon="/assets/Icon/Navbar/Navbar_searchIcon.svg"
                />
           

                {/*   Soon  */}
                <BlurredMapImageWrapper>
                    <BlurredMapImage
                        src="assets/Map/Map_for_blur.png"
                        alt="Map_for_blur"
                        blur //  
                    />
                    <SoonOverlay src="/assets/Map/Soon.gif" alt="Soon" />
                </BlurredMapImageWrapper>
            </MainWrapper>
        </>
    );
}

export default Preparing;
